$black: #18191F;
$accent: #8C30F5;

* {
  box-sizing: border-box;
}

body {
  font-family: 'Inter', sans-serif;;
  margin: 0;
}

a {
  transition: 0.3s ease;
  text-decoration: none;
}

.section {}

.section h2 {
  margin-top: 0;
  font-weight: 800;
  font-size: 48px;
  text-align: center;
  color: #FFFFFF;
}

.block {
  display: block;
}

.main {
  width: 100%;
  min-height: 100vh;
}

.container {
  margin: 0 auto;
  width: 79%;
}

.button {
  width: 100%;
  display: flex;
  min-width: 100px;
  padding-left: 12px;
  padding-right: 12px;
  align-items: center;
  justify-content: center;
  height: 40px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: bold;
  color: #fff;
}

.button--primary {
  background: #8C30F5;
  color: #fff;
}

.button--secondary {
  background: #F1E4FF;
  color: #8C30F5;
}

.button.button--narrow {
  max-width: 100px;
}

.button.button--main_content {
  height: 62px;
  min-width: 188px;
  font-size: 20px;
}

//////////////////////////////
.header {
  height: 72px;
  display: flex;
  align-items: center;

  .logo {
    margin-right: 20%;
  }
}

.header_nav a {
  font-size: 14px;
  font-weight: 500;
  color: $black;
}

.header_nav a + a {
  margin-left: 30px;
}

.header_nav a:hover {
  color: $accent;
}

.header_buttons {
  margin-left: auto;
  display: flex;
  align-items: center;
}

.header_buttons a + a {
  margin-left: 14px;
}

.main_content {
  position: relative;
  min-height: 100%;
  width: 100%;
  background-repeat: no-repeat;
  background-position: bottom center;
  background-image: url("data:image/svg+xml,%3Csvg width='1200' height='170' viewBox='0 0 1200 170' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='600' cy='600' r='600' fill='%23F7EFFF'/%3E%3C/svg%3E%0A");
}

.main_content_container {
  width: 70%;
  height: 100%;
}

.main_content h1 {
  margin-top: 10%;
  margin-bottom: 5%;
  font-size: 72px;
  font-weight: bold;
  color: $black;
  text-align: center;
}

.main_content .phone {
  margin-top: 140px;
  display: flex;
  justify-content: center;
  position: relative;
  margin: 0 auto;
}

.main_content_login {
  display: flex;
  justify-content: center;
  margin-bottom: 10%;
}

.main_content .phone img {
  width: 280px;
}

//////////

.carousel {
  padding-top: 60px;
  padding-bottom: 60px;
  background-color: #8C30F5;
}

.carousel_meta {
  text-align: center;
}

.carousel_meta p {
  margin: 16px 0 48px 0;
  color: #fff;
  text-align: center;
  font-size: 18px;
  line-height: 32px;
  font-style: normal;
  font-weight: normal;
}

@keyframes scroll {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(-1000%, 0)
  }
}

.marquee {
  display: flex;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
}

.carousel_content_item {
  animation: scroll 20s infinite linear;
  flex-grow: 1;
  height: 96px;
  min-width: 170px;
  display: flex;
  align-self: center;
  justify-content: center;
  background-color: #fff;
  box-shadow: 0px 30px 40px rgba(212, 217, 232, 0.2);
  border-radius: 16px;
}

.carousel_content_item + .carousel_content_item {
  margin-left: 48px;
}

.dashboard {
  position: relative;
  min-height: 800px;
}

.dashboard .container {
  min-height: 600px;
}

.dashboard .container .content {
  display: flex;
  padding-top: 64px;
}

.dashboard .container .content h2 {
  color: #18191F;
  display: flex;
  margin-right: 30px;
  text-align: left;
}

.dashboard .container .content p {
  width: 45%;
  font-size: 18px;
  line-height: 32px;
  color: #43444B;
}

.dashboard .decorator {
  position: absolute;
  bottom: 0;
  height: 390px;
  width: 100%;
  background-color: #F6EFFF;
}

.dashboard .image {
  display: block;
  text-align: center;
  margin: 0 auto;
  max-width: 768px;
  position: relative;
  z-index: 10;
  margin-top: 48px;
}

////////////////////////

.advantages {
  padding-top: 80px;
  padding-bottom: 80px;
  background-color: #050028;
}

.advantages h2 {
  width: 40%;
  margin: 0 auto;
  margin-bottom: 48px;
}

.advantages_cards {
  display: flex;
  margin-bottom: 48px;
}

.advantages_cards .item {
  padding: 40px;
  border-radius: 8px;
  background-color: #fff;
}

.advantages_cards .item + .item {
  margin-left: 30px;
}

.advantages_cards .item h3 {
  font-weight: 600;
  font-size: 24px;
  color: #18191F;
  margin: 0;
  margin-top: 50px;
  margin-bottom: 18px;
}

.advantages_cards .item p {
  margin: 0;
  font-size: 16px;
  color: #18191F;
  line-height: 26px;
}

.advantages .button_wrapper {
  display: flex;
  align-self: center;
  justify-content: center;
}

.selling {
  padding-top: 80px;
  padding-bottom: 80px;
}

.selling h2 {
  text-align: left;
  color: #18191F;
}

.selling .apps {
  display: flex;
  margin-top: 48px;
}

.selling .apps .card + .card {
  margin-left: 30px;
}

.selling .item {
  background-repeat: no-repeat;
  background-size: 50%;
  min-height: 540px;
}

.selling .item.blue {
  background-color: #A0DCFF;
  background-position: 50% -25%;
}

.selling .item.pink {
  background-color: #FDD9D9;
  background-size: 90%;
  background-position: 200% 100%;
}

.selling .card {
  background-repeat: no-repeat;
  width: 50%;
  min-height: 540px;
  border-radius: 8px;
  background-image: #fff;
}

.selling .card h3 {
  font-size: 24px;
}

//////////////

.video {
  padding: 80px 0;
  background-color: #F4F5F7;
}

.video h2 {
  margin: 0 auto;
  text-align: center;
  width: 50%;
  color: #18191F;
  margin-bottom: 48px;
  line-height: 64px;
}

.video .video_player {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 550px;
  width: 100%;
  background-position: center;
  background-repeat: no-repeat;
}

.video .video_player .play_button {
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100px;
  width: 100px;
  background-color: #8C30F5;
  opacity: 0.9;
  color: #fff;
  font-size: 24px;
}

.video .video_description {
  width: 80%;
  margin: 0 auto;
  margin-top: 48px;
  display: flex;
}

.video .video_description h3 {
  font-weight: 800;
  font-size: 28px;
  line-height: 40px;
  color: #18191F;
}

.video .video_description p {
  font-size: 16px;
  line-height: 26px;
  color: #43444B;
}

.video .video_description > * {
  width: 50%;
}

.video .video_description > * + * {
  width: 50%;
  margin-left: 30px;
}

.faq {
  padding: 80px 0;
}

.faq h2 {
  color: #18191F;
  margin-bottom: 48px;
  font-size: 48px;
}

.faq .faq_list {
  margin-bottom: 48px;
}

.faq .faq_list .item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 42px;
  background: #F4F5F7;
  border-radius: 8px;
}

.faq .faq_list .item + .item {
  margin-top: 16px;
}

.faq .faq_list .item p {
  margin: 0;
  color: #18191F;
  font-size: 20px;
  line-height: 30px;
}

.faq .button_wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
}

.faq .button_wrapper .button {
  min-width: 260px;
  font-weight: normal;
  font-size: 20px;
}

.footer {
  background-color: #0B0D17;
}

.footer .footer_bottom {
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 28px 0;
}

.footer .footer_bottom > .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer .footer_bottom p {
  font-size: 14px;
  line-height: 24px;
  color: #D9DBE1;
  margin: 0;
}

.footer .footer_bottom nav {
  display: flex;
  align-items: center;
}

.footer .footer_bottom nav a {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(255, 255, 255, 0.1);
  padding: 8px;
  border-radius: 50%;
}

.footer .footer_column.first {
  max-width: 260px;
}

.footer .footer_column.second {
  margin-left: 20%;
  max-width: 260px;
}

.footer .footer_links {
  padding-top: 64px;
  padding-bottom: 64px;
}

.footer .footer_links h4 {
  color: #F4F5F7;
  font-size: 18px;
  line-height: 28px;
  margin-top: 0px;
  margin-bottom: 24px;
  font-weight: bold;
}

.footer .footer_links nav {
  display: flex;
  flex-direction: column;
}

.footer .footer_links nav a {
  color: #EEEFF4;
  font-size: 14px;
  line-height: 24px;
}

.footer .footer_links nav a + a {
  margin-top: 12px;
} 

.footer .footer_bottom nav a + a {
  margin-left: 20px;
}

.footer .stores {
  margin-left: auto;
  display: flex;
  flex-direction: column;
}

.footer > .container {
  display: flex;
}

.footer .stores a + a {
  margin-top: 8px;
}

.mobile-menu {
  display: none;
  width: 100%;
  position: fixed;
  top: 72px;
  left: 0;
  height: calc(100vh - 72px);
  background-color: #050028;
  z-index: 10;
}

.mobile-menu nav {
  padding: 80px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.mobile-menu nav a {
  font-size: 24px;
  line-height: 20px;
  color: #F0EEFF;
  font-weight: bold;
}

.mobile-menu nav a + a {
  margin-top: 36px;
}

.hamburger {
  display: none !important;
  margin-left: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  min-height: 40px;
  min-width: 40px;
  border-radius: 8px;
  background-color: #8C30F5;
}

.hamburger .cross {
  display: none;
}

.menu_active .hamburger .cross {
  display: initial;
}

.menu_active .hamburger .lines {
  display: none;
}

.menu_active .header {
  position: fixed;
  overflow: hidden;
  width: 100%;
  padding-left: 5%;
  padding-right: 5%;
  left: 0;
  top: 0px;
  z-index: 90;
  background-color: #fff;
}

.menu_active .mobile-menu {
  display: block;
}

.menu_active .mobile-menu .buttons {
  display: flex;
  justify-content: center;
}

.menu_active .mobile-menu .buttons > * {
  width: 100px;
}

.menu_active .mobile-menu .buttons > * + * {
  margin-left: 8px;
}

@media (max-width: 769px) {
  .hamburger {
    display: flex !important;
  }

  .mobile-menu.active {
    display: block;
  }

  .container {
    width: 90%;
  }

  .main {
    min-height: 85vh;
  }

  .section h2 {
    font-size: 48px;
  }

  .main_content h1 {
    margin-top: 10%;
    margin-bottom: 5%;
    font-size: 56px;
  }

  .main_content_container {
    width: 80%;
  }

  .header_nav {
    display: none;
  }

  .header_buttons {
    display: none;
  }

  .dashboard .image {
    max-width: 600px;
  }

  .dashboard .container .content {
    flex-direction: column;
    text-align: left;
  }

  .dashboard .container .content p {
    width: 100%;
  }

  .advantages h2 {
    width: 100%;
  }

  .advantages_cards .item {
    padding: 40px 28px;
  }

  .advantages_cards .item + .item {
    margin-left: 18px;
  }

  .selling .apps {
    flex-direction: column;
  }

  .selling .card {
    width: 100%;
  }

  .selling .item {
    min-height: 380px;
  }

  .selling .apps .card + .card {
    margin-left: 0;
  }

  .video h2 {
    width: 100%;
  }

  .video .video_description {
    width: 100%;
    flex-direction: column;
  }

  .video .video_description p {
    margin-left: 0;
    width: 100%;
  }
}

@media (max-width: 480px) {
  .section h2 {
    font-size: 36px;
  }

  .container {
    width: 80%;
  }

  .main_content h1 {
    margin-top: 10%;
    margin-bottom: 5%;
    font-size: 36px;
  }
  
  .dashboard .image {
    width: 100%;
    height: auto;
  }

  .advantages_cards {
    flex-direction: column;
  }

  .advantages_cards .item + .item {
    margin-left: 0;
    margin-top: 18px;
  }

  .footer > .container {
    flex-direction: column;
    align-items: center;
  }

  .footer .footer_column {
    text-align: center;
  }

  .footer .footer_column.second {
    margin-left: 0;
    margin-top: 48px;
  }

  .footer .stores {
    flex-direction: row;
    margin-left: 0;
    margin-top: 54px;
  }

  .footer .stores a + a {
    margin-top: 0;
    margin-left: 16px;
  }

  .footer .footer_bottom > .container {
    flex-direction: column;
  }

  .footer .footer_bottom nav {
    margin-top: 24px;
  }

  .footer .footer_bottom nav a {
    height: 32px;
    width: 32px;
  }

  .video .video_player {
    border-radius: 8px;
  }

  .faq .faq_list {
    margin-bottom: 0;
  }

  .faq .button_wrapper {
    display: none;
  }

  .video .video_description h3 {
    margin-top: 0;
  }

  .video .video_player {
    min-height: 260px;
  }

  .video .video_description > * {
    width: 100%;
  }

  .video .video_player .play_button {
    width: 72px;
    height: 72px;
  }

  .menu_active .header {
    padding-left: 10%;
    padding-right: 10%;
  }

  .dashboard .decorator {
    height: 220px;
  }
}